  wide: /^(’∞’∏÷Ç’∂’æ’°÷Ä|÷É’•’ø÷Ä’æ’°÷Ä|’¥’°÷Ä’ø|’°’∫÷Ä’´’¨|’¥’°’µ’´’Ω|’∞’∏÷Ç’∂’´’Ω|’∞’∏÷Ç’¨’´’Ω|÷Ö’£’∏’Ω’ø’∏’Ω|’Ω’•’∫’ø’•’¥’¢’•÷Ä|’∞’∏’Ø’ø’•’¥’¢’•÷Ä|’∂’∏’µ’•’¥’¢’•÷Ä|’§’•’Ø’ø’•’¥’¢’•÷Ä)/i,
