      singularNominative: "–º–µ–Ω—å—à–µ, —á–µ–º —á–µ—Ä–µ–∑ {{count}} –º–∏–Ω—É—Ç—É",
      singularGenitive: "–º–µ–Ω—å—à–µ, —á–µ–º —á–µ—Ä–µ–∑ {{count}} –º–∏–Ω—É—Ç—ã",
      pluralGenitive: "–º–µ–Ω—å—à–µ, —á–µ–º —á–µ—Ä–µ–∑ {{count}} –º–∏–Ω—É—Ç",
