import { buildLocalizeFn } from "../../_lib/buildLocalizeFn.mjs";

const eraValues = {
  narrow: ["–ø—Ä.–Ω.–µ.", "–Ω.–µ."],
  abbreviated: ["–ø—Ä–µ–¥–∏ –Ω. –µ.", "–Ω. –µ."],
  wide: ["–ø—Ä–µ–¥–∏ –Ω–æ–≤–∞—Ç–∞ –µ—Ä–∞", "–Ω–æ–≤–∞—Ç–∞ –µ—Ä–∞"],
};

const quarterValues = {
  narrow: ["1", "2", "3", "4"],
  abbreviated: ["1-–≤–æ —Ç—Ä–∏–º–µ—Å.", "2-—Ä–æ —Ç—Ä–∏–º–µ—Å.", "3-—Ç–æ —Ç—Ä–∏–º–µ—Å.", "4-—Ç–æ —Ç—Ä–∏–º–µ—Å."],

  wide: [
    "1-–≤–æ —Ç—Ä–∏–º–µ—Å–µ—á–∏–µ",
    "2-—Ä–æ —Ç—Ä–∏–º–µ—Å–µ—á–∏–µ",
    "3-—Ç–æ —Ç—Ä–∏–º–µ—Å–µ—á–∏–µ",
    "4-—Ç–æ —Ç—Ä–∏–º–µ—Å–µ—á–∏–µ",
  ],
};

const monthValues = {
  abbreviated: [
    "—è–Ω—É",
    "—Ñ–µ–≤",
    "–º–∞—Ä",
    "–∞–ø—Ä",
    "–º–∞–π",
    "—é–Ω–∏",
    "—é–ª–∏",
    "–∞–≤–≥",
    "—Å–µ–ø",
    "–æ–∫—Ç",
    "–Ω–æ–µ",
    "–¥–µ–∫",
  ],

  wide: [
    "—è–Ω—É–∞—Ä–∏",
    "—Ñ–µ–≤—Ä—É–∞—Ä–∏",
    "–º–∞—Ä—Ç",
    "–∞–ø—Ä–∏–ª",
    "–º–∞–π",
    "—é–Ω–∏",
    "—é–ª–∏",
    "–∞–≤–≥—É—Å—Ç",
    "—Å–µ–ø—Ç–µ–º–≤—Ä–∏",
    "–æ–∫—Ç–æ–º–≤—Ä–∏",
    "–Ω–æ–µ–º–≤—Ä–∏",
    "–¥–µ–∫–µ–º–≤—Ä–∏",
  ],
};

const dayValues = {
  narrow: ["–ù", "–ü", "–í", "–°", "–ß", "–ü", "–°"],
  short: ["–Ω–¥", "–ø–Ω", "–≤—Ç", "—Å—Ä", "—á—Ç", "–ø—Ç", "—Å–±"],
  abbreviated: ["–Ω–µ–¥", "–ø–æ–Ω", "–≤—Ç–æ", "—Å—Ä—è", "—á–µ—Ç", "–ø–µ—Ç", "—Å—ä–±"],
  wide: [
    "–Ω–µ–¥–µ–ª—è",
    "–ø–æ–Ω–µ–¥–µ–ª–Ω–∏–∫",
    "–≤—Ç–æ—Ä–Ω–∏–∫",
    "—Å—Ä—è–¥–∞",
    "—á–µ—Ç–≤—ä—Ä—Ç—ä–∫",
    "–ø–µ—Ç—ä–∫",
    "—Å—ä–±–æ—Ç–∞",
  ],
};

const dayPeriodValues = {
  wide: {
    am: "–ø—Ä–µ–¥–∏ –æ–±—è–¥",
    pm: "—Å–ª–µ–¥ –æ–±—è–¥",
    midnight: "–≤ –ø–æ–ª—É–Ω–æ—â",
    noon: "–Ω–∞ –æ–±—è–¥",
    morning: "—Å—É—Ç—Ä–∏–Ω—Ç–∞",
    afternoon: "—Å–ª–µ–¥–æ–±–µ–¥",
    evening: "–≤–µ—á–µ—Ä—Ç–∞",
    night: "–ø—Ä–µ–∑ –Ω–æ—â—Ç–∞",
  },
};

function isFeminine(unit) {
  return (
    unit === "year" || unit === "week" || unit === "minute" || unit === "second"
  );
}

function isNeuter(unit) {
  return unit === "quarter";
}

function numberWithSuffix(number, unit, masculine, feminine, neuter) {
  const suffix = isNeuter(unit)
    ? neuter
    : isFeminine(unit)
      ? feminine
      : masculine;
  return number + "-" + suffix;
}

const ordinalNumber = (dirtyNumber, options) => {
  const number = Number(dirtyNumber);
  const unit = options?.unit;

  if (number === 0) {
    return numberWithSuffix(0, unit, "–µ–≤", "–µ–≤–∞", "–µ–≤–æ");
  } else if (number % 1000 === 0) {
    return numberWithSuffix(number, unit, "–µ–Ω", "–Ω–∞", "–Ω–æ");
  } else if (number % 100 === 0) {
    return numberWithSuffix(number, unit, "—Ç–µ–Ω", "—Ç–Ω–∞", "—Ç–Ω–æ");
  }

  const rem100 = number % 100;
  if (rem100 > 20 || rem100 < 10) {
    switch (rem100 % 10) {
      case 1:
        return numberWithSuffix(number, unit, "–≤–∏", "–≤–∞", "–≤–æ");
      case 2:
        return numberWithSuffix(number, unit, "—Ä–∏", "—Ä–∞", "—Ä–æ");
      case 7:
      case 8:
        return numberWithSuffix(number, unit, "–º–∏", "–º–∞", "–º–æ");
    }
  }

  return numberWithSuffix(number, unit, "—Ç–∏", "—Ç–∞", "—Ç–æ");
};

export const localize = {
  ordinalNumber,

  era: buildLocalizeFn({
    values: eraValues,
    defaultWidth: "wide",
  }),

  quarter: buildLocalizeFn({
    values: quarterValues,
    defaultWidth: "wide",
    argumentCallback: (quarter) => quarter - 1,
  }),

  month: buildLocalizeFn({
    values: monthValues,
    defaultWidth: "wide",
  }),

  day: buildLocalizeFn({
    values: dayValues,
    defaultWidth: "wide",
  }),

  dayPeriod: buildLocalizeFn({
    values: dayPeriodValues,
    defaultWidth: "wide",
  }),
};
