import { buildLocalizeFn } from "../../_lib/buildLocalizeFn.mjs";

const eraValues = {
  narrow: ["–ø—Ä.–Ω.–µ.", "–Ω.–µ."],
  abbreviated: ["–ø—Ä–µ–¥ –Ω. –µ.", "–Ω. –µ."],
  wide: ["–ø—Ä–µ–¥ –Ω–∞—à–∞—Ç–∞ –µ—Ä–∞", "–Ω–∞—à–∞—Ç–∞ –µ—Ä–∞"],
};

const quarterValues = {
  narrow: ["1", "2", "3", "4"],
  abbreviated: ["1-–≤–∏ –∫–≤.", "2-—Ä–∏ –∫–≤.", "3-—Ç–∏ –∫–≤.", "4-—Ç–∏ –∫–≤."],
  wide: ["1-–≤–∏ –∫–≤–∞—Ä—Ç–∞–ª", "2-—Ä–∏ –∫–≤–∞—Ä—Ç–∞–ª", "3-—Ç–∏ –∫–≤–∞—Ä—Ç–∞–ª", "4-—Ç–∏ –∫–≤–∞—Ä—Ç–∞–ª"],
};

const monthValues = {
  abbreviated: [
    "—ò–∞–Ω",
    "—Ñ–µ–≤",
    "–º–∞—Ä",
    "–∞–ø—Ä",
    "–º–∞—ò",
    "—ò—É–Ω",
    "—ò—É–ª",
    "–∞–≤–≥",
    "—Å–µ–ø—Ç",
    "–æ–∫—Ç",
    "–Ω–æ–µ–º",
    "–¥–µ–∫",
  ],

  wide: [
    "—ò–∞–Ω—É–∞—Ä–∏",
    "—Ñ–µ–≤—Ä—É–∞—Ä–∏",
    "–º–∞—Ä—Ç",
    "–∞–ø—Ä–∏–ª",
    "–º–∞—ò",
    "—ò—É–Ω–∏",
    "—ò—É–ª–∏",
    "–∞–≤–≥—É—Å—Ç",
    "—Å–µ–ø—Ç–µ–º–≤—Ä–∏",
    "–æ–∫—Ç–æ–º–≤—Ä–∏",
    "–Ω–æ–µ–º–≤—Ä–∏",
    "–¥–µ–∫–µ–º–≤—Ä–∏",
  ],
};

const dayValues = {
  narrow: ["–ù", "–ü", "–í", "–°", "–ß", "–ü", "–°"],
  short: ["–Ω–µ", "–ø–æ", "–≤—Ç", "—Å—Ä", "—á–µ", "–ø–µ", "—Å–∞"],
  abbreviated: ["–Ω–µ–¥", "–ø–æ–Ω", "–≤—Ç–æ", "—Å—Ä–µ", "—á–µ—Ç", "–ø–µ—Ç", "—Å–∞–±"],
  wide: [
    "–Ω–µ–¥–µ–ª–∞",
    "–ø–æ–Ω–µ–¥–µ–ª–Ω–∏–∫",
    "–≤—Ç–æ—Ä–Ω–∏–∫",
    "—Å—Ä–µ–¥–∞",
    "—á–µ—Ç–≤—Ä—Ç–æ–∫",
    "–ø–µ—Ç–æ–∫",
    "—Å–∞–±–æ—Ç–∞",
  ],
};

const dayPeriodValues = {
  wide: {
    am: "–ø—Ä–µ—Ç–ø–ª–∞–¥–Ω–µ",
    pm: "–ø–æ–ø–ª–∞–¥–Ω–µ",
    midnight: "–ø–æ–ª–Ω–æ—ú",
    noon: "–Ω–∞–ø–ª–∞–¥–Ω–µ",
    morning: "–Ω–∞—É—Ç—Ä–æ",
    afternoon: "–ø–æ–ø–ª–∞–¥–Ω–µ",
    evening: "–Ω–∞–≤–µ—á–µ—Ä",
    night: "–Ω–æ—ú–µ",
  },
};

const ordinalNumber = (dirtyNumber, _options) => {
  const number = Number(dirtyNumber);

  const rem100 = number % 100;
  if (rem100 > 20 || rem100 < 10) {
    switch (rem100 % 10) {
      case 1:
        return number + "-–≤–∏";
      case 2:
        return number + "-—Ä–∏";
      case 7:
      case 8:
        return number + "-–º–∏";
    }
  }
  return number + "-—Ç–∏";
};

export const localize = {
  ordinalNumber,

  era: buildLocalizeFn({
    values: eraValues,
    defaultWidth: "wide",
  }),

  quarter: buildLocalizeFn({
    values: quarterValues,
    defaultWidth: "wide",
    argumentCallback: (quarter) => quarter - 1,
  }),

  month: buildLocalizeFn({
    values: monthValues,
    defaultWidth: "wide",
  }),

  day: buildLocalizeFn({
    values: dayValues,
    defaultWidth: "wide",
  }),

  dayPeriod: buildLocalizeFn({
    values: dayPeriodValues,
    defaultWidth: "wide",
  }),
};
