const ordinalNumber = (dirtyNumber, options) => {
  const number = Number(dirtyNumber);
  const unit = options?.unit;

  if (unit === "quarter") {
    // many news outlets use "qu√Ω I"...
    switch (number) {
      case 1:
        return "I";
      case 2:
        return "II";
      case 3:
        return "III";
      case 4:
        return "IV";
    }
  } else if (unit === "day") {
    // day of week in Vietnamese has ordinal number meaning,
    // so we should use them, else it'll sound weird
    switch (number) {
      case 1:
        return "th·ª© 2"; // meaning 2nd day but it's the first day of the week :D
      case 2:
        return "th·ª© 3"; // meaning 3rd day
      case 3:
        return "th·ª© 4"; // meaning 4th day and so on
      case 4:
        return "th·ª© 5";
      case 5:
        return "th·ª© 6";
      case 6:
        return "th·ª© 7";
      case 7:
        return "ch·ªß nh·∫≠t"; // meaning Sunday, there's no 8th day :D
    }
  } else if (unit === "week") {
    if (number === 1) {
      return "th·ª© nh·∫•t";
    } else {
      return "th·ª© " + number;
    }
  } else if (unit === "dayOfYear") {
    if (number === 1) {
      return "ƒë·∫ßu ti√™n";
    } else {
      return "th·ª© " + number;
    }
  }

  // there are no different forms of ordinal numbers in Vietnamese
  return String(number);
};

