export const labelWeekNumber = ({
  localeCode,
  week,
}: {
  localeCode?: string;
  week: number;
}): string => {
  switch (localeCode) {
    case "nb":
      return `Uke ${week}`;
    case "nn":
      return `Veke ${week}`;
    case "en-GB":
      return `Week ${week}`;
    default:
      return `Uke ${week}`;
  }
};

