export const modalLabel = (
  localeCode: string,
  variant: "single" | "multiple" | "range" | "month",
) => {
  switch (variant) {
    case "single":
      return modalLabelSingle(localeCode);
    case "multiple":
      return modalLabelMultiple(localeCode);
    case "range":
      return modalLabelRanged(localeCode);
    case "month":
      return modalLabelMonth(localeCode);
    default:
      return modalLabelSingle(localeCode);
  }
};

