import { buildLocalizeFn } from "../../_lib/buildLocalizeFn.mjs";

const eraValues = {
  narrow: ["–ú.–ê", "–ú"],
  abbreviated: ["–ú.–ê", "–ú"],
  wide: ["–ú–∏–ª–æ–¥–¥–∞–Ω –ê–≤–≤–∞–ª–≥–∏", "–ú–∏–ª–æ–¥–∏–π"],
};

const quarterValues = {
  narrow: ["1", "2", "3", "4"],
  abbreviated: ["1-—á–æ—Ä.", "2-—á–æ—Ä.", "3-—á–æ—Ä.", "4-—á–æ—Ä."],
  wide: ["1-—á–æ—Ä–∞–∫", "2-—á–æ—Ä–∞–∫", "3-—á–æ—Ä–∞–∫", "4-—á–æ—Ä–∞–∫"],
};

const monthValues = {
  narrow: ["–Ø", "–§", "–ú", "–ê", "–ú", "–ò", "–ò", "–ê", "–°", "–û", "–ù", "–î"],
  abbreviated: [
    "—è–Ω–≤",
    "—Ñ–µ–≤",
    "–º–∞—Ä",
    "–∞–ø—Ä",
    "–º–∞–π",
    "–∏—é–Ω",
    "–∏—é–ª",
    "–∞–≤–≥",
    "—Å–µ–Ω",
    "–æ–∫—Ç",
    "–Ω–æ—è",
    "–¥–µ–∫",
  ],

  wide: [
    "—è–Ω–≤–∞—Ä",
    "—Ñ–µ–≤—Ä–∞–ª",
    "–º–∞—Ä—Ç",
    "–∞–ø—Ä–µ–ª",
    "–º–∞–π",
    "–∏—é–Ω",
    "–∏—é–ª",
    "–∞–≤–≥—É—Å—Ç",
    "—Å–µ–Ω—Ç–∞–±—Ä",
    "–æ–∫—Ç–∞–±—Ä",
    "–Ω–æ—è–±—Ä",
    "–¥–µ–∫–∞–±—Ä",
  ],
};

const dayValues = {
  narrow: ["–Ø", "–î", "–°", "–ß", "–ü", "–ñ", "–®"],
  short: ["—è–∫", "–¥—É", "—Å–µ", "—á–æ", "–ø–∞", "–∂—É", "—à–∞"],
  abbreviated: ["—è–∫—à", "–¥—É—à", "—Å–µ—à", "—á–æ—Ä", "–ø–∞–π", "–∂—É–º", "—à–∞–Ω"],
  wide: [
    "—è–∫—à–∞–Ω–±–∞",
    "–¥—É—à–∞–Ω–±–∞",
    "—Å–µ—à–∞–Ω–±–∞",
    "—á–æ—Ä—à–∞–Ω–±–∞",
    "–ø–∞–π—à–∞–Ω–±–∞",
    "–∂—É–º–∞",
    "—à–∞–Ω–±–∞",
  ],
};

const dayPeriodValues = {
  any: {
    am: "–ü.–û.",
    pm: "–ü.–ö.",
    midnight: "—è—Ä–∏–º —Ç—É–Ω",
    noon: "–ø–µ—à–∏–Ω",
    morning: "—ç—Ä—Ç–∞–ª–∞–±",
    afternoon: "–ø–µ—à–∏–Ω–¥–∞–Ω –∫–µ–π–∏–Ω",
    evening: "–∫–µ—á–∞—Å–∏",
    night: "—Ç—É–Ω",
  },
};

const formattingDayPeriodValues = {
  any: {
    am: "–ü.–û.",
    pm: "–ü.–ö.",
    midnight: "—è—Ä–∏–º —Ç—É–Ω",
    noon: "–ø–µ—à–∏–Ω",
    morning: "—ç—Ä—Ç–∞–ª–∞–±",
    afternoon: "–ø–µ—à–∏–Ω–¥–∞–Ω –∫–µ–π–∏–Ω",
    evening: "–∫–µ—á–∞—Å–∏",
    night: "—Ç—É–Ω",
  },
};

const ordinalNumber = (dirtyNumber, _options) => {
  return String(dirtyNumber);
};

export const localize = {
  ordinalNumber,

  era: buildLocalizeFn({
    values: eraValues,
    defaultWidth: "wide",
  }),

  quarter: buildLocalizeFn({
    values: quarterValues,
    defaultWidth: "wide",
    argumentCallback: (quarter) => quarter - 1,
  }),

  month: buildLocalizeFn({
    values: monthValues,
    defaultWidth: "wide",
  }),

  day: buildLocalizeFn({
    values: dayValues,
    defaultWidth: "wide",
  }),

  dayPeriod: buildLocalizeFn({
    values: dayPeriodValues,
    defaultWidth: "any",
    formattingValues: formattingDayPeriodValues,
    defaultFormattingWidth: "any",
  }),
};
